.App{box-sizing:border-box;min-height:70vh;max-height:70vh;width:70%;margin:15vh auto 0;padding:20px 20px 0;text-align:center;display:flex;align-items:center;flex-direction:column;background-color:#f5f5f5;box-shadow:0px 0px 30px #dedede}.App h1{font-weight:300;font-size:55px;color:#e9d9d8}.container{margin:20px 0 0;width:90%;height:calc(70vh - 180px)}.container::after{background-color:#f5f5f5;height:10px;width:200px}.content{box-shadow:0px 0px 10px #dedede}.content__input-section{box-sizing:border-box;display:flex;padding-left:5px;background-color:#fefefe}.content__input-section__btn-active{background-color:#fefefe;border:none;color:#e6e6e6;font-size:20px}.content__input-section__btn-active::before{content:"ᐱ"}.content__input-section__btn-active:hover{cursor:pointer}.content__input-section__btn-hidden{background-color:#fefefe;border:none;color:#e6e6e6;font-size:20px}.content__input-section__btn-hidden::before{content:"ᐯ"}.content__input-section__btn-hidden:hover{cursor:pointer}.content__input-section input{box-sizing:border-box;background-color:#fefefe;border:none;width:100%;height:50px;padding:5px;outline:none;font-weight:300;font-size:24px;color:#4d4d4d}.content__input-section input::placeholder{color:#e6e6e6;font-size:24px}@media screen and (max-width: 1100px){.App{width:80%}}@media screen and (max-width: 800px){.content__input-section input{font-size:20px}.content__input-section input::placeholder{font-size:20px}}@media screen and (max-width: 400px){.App{width:90%}.App h1{font-size:30px}}@media screen and (max-width: 350px){.App{width:100%}}@media only screen and (device-width: 414px)and (device-height: 896px)and (-webkit-device-pixel-ratio: 2){.App{width:95%}}